function getWords() {
    for (let i = palavras.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [palavras[i], palavras[j]] = [palavras[j], palavras[i]];
    }
    return palavras;
}

export default getWords


